@charset "UTF-8";
@font-face {
  font-family: "PTMono";
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "RobotoCondensed";
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "RobotoCondensed";
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.html {
  height: 100%;
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16.41px;
  font-weight: 400;
  font-style: normal;
  scroll-behavior: smooth;
  margin: 0 auto;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.body {
  height: 100%;
  display: flex;
  flex-flow: column;
  margin: 0;
}

.main {
  flex: 1 0 auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  padding: 0;
  margin: -1px;
}

a {
  text-decoration: none;
  color: rgb(0, 0, 0);
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

.preloader {
  position: fixed;
  inset: 0;
  z-index: 1001;
}

.preloader__container {
  position: relative;
  top: 50%;
  left: 50%;
  width: 70px;
  height: 70px;
  margin-top: -35px;
  margin-left: -35px;
  text-align: center;
  animation: preloader-rotate 2s infinite linear;
}

.preloader__item {
  position: absolute;
  display: inline-block;
  top: 0;
  background-color: skyblue;
  border-radius: 100%;
  width: 35px;
  height: 35px;
  animation: preloader-bounce 2s infinite ease-in-out;
}

.preloader__item:last-child {
  top: auto;
  bottom: 0;
  animation-delay: -1s;
}

@keyframes preloader-rotate {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes preloader-bounce {
  0%, 100% {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
}
:root {
  --calculation-of-one-pixel: calc(1vw + 1vh);
  --pixel: calc(var(--calculation-of-one-pixel) * 0.041789);
}

html, body, .page {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  transition: all 0.8s cubic-bezier(0.54, 0.35, 0.29, 0.99);
  -webkit-transition: all 0.8s cubic-bezier(0.54, 0.35, 0.29, 0.99);
  -moz-transition: all 0.8s cubic-bezier(0.54, 0.35, 0.29, 0.99);
  -o-transition: all 0.8s cubic-bezier(0.54, 0.35, 0.29, 0.99);
  color: #fff;
  overflow: hidden;
}

* {
  font-family: Georgia, "Times New Roman", Times, serif;
}

.page {
  position: absolute;
  padding-top: calc(var(--pixel) * 30);
  padding-left: calc(var(--pixel) * 30);
}

#p1 {
  left: 0;
}

#p2, #p3, #p4, #p5 {
  left: 200%;
}

#p1 {
  background: rgb(118, 101, 224);
}

#p2 {
  background: #ffc194;
}

#p3 {
  background: #ffe675;
}

#p4 {
  background: rgb(253, 129, 195);
}

#p5 {
  background: rgb(194, 137, 255);
}

#t2:target #p2,
#t3:target #p3,
#t4:target #p4,
#t5:target #p5 {
  transform: translateX(-190%);
  -webkit-transform: translateX(-190%);
  -moz-transform: translateX(-190%);
  -o-transform: translateX(-190%);
  transition-delay: 0.4s !important;
}

#t2:target #p1,
#t3:target #p1,
#t4:target #p1,
#t5:target #p1 {
  background: black;
}

#t2:target #p1 .menu__icon,
#t3:target #p1 .menu__icon,
#t4:target #p1 .menu__icon,
#t5:target #p1 .menu__icon {
  -webkit-filter: blur(calc(var(--pixel) * 3));
}

.menu__icon {
  color: #fff;
  font-size: calc(var(--pixel) * 23);
  display: block;
}

ul .menu__icon:hover {
  opacity: 0.5;
}

.page .menu__icon .title {
  line-height: calc(var(--pixel) * 50);
}

#t2:target ul .menu__icon,
#t3:target ul .menu__icon,
#t4:target ul .menu__icon,
#t5:target ul .menu__icon {
  transform: scale(0.6);
  -webkit-transform: scale(0.6);
  -moz-transform: scale(0.6);
  -o-transform: scale(0.6);
  transition-delay: 0.25s;
}

#t2:target #dos,
#t3:target #tres,
#t4:target #cuatro,
#t4:target #cinco {
  transform: scale(1.2) !important;
  -webkit-transform: scale(1.2) !important;
  -moz-transform: scale(1.2) !important;
  -o-transform: scale(1.2) !important;
}

ul {
  position: fixed;
  z-index: 1;
  top: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  height: calc(var(--pixel) * 280);
  width: 10%;
  padding: 0;
  text-align: center;
}

#menu .menu__icon {
  margin: calc(var(--pixel) * 30) 0;
  transition: all 0.5s ease-out !important;
  -webkit-transition: all 0.5s ease-out;
  -moz-transition: all 0.5s ease-out;
  -o-transition: all 0.5s ease-out;
}

a {
  text-decoration: none;
}

.title, .hint {
  display: block;
  color: black;
}

.title {
  font-size: calc(var(--pixel) * 36);
  width: calc(var(--pixel) * 500);
}

.hint {
  font-size: calc(var(--pixel) * 22);
  width: calc(var(--pixel) * 500);
}

#p4 .hint {
  display: inherit !important;
}

.hint a {
  color: yellow;
  transition: all 250ms ease-out;
  -webkit-transition: all 250ms ease-out;
  -moz-transition: all 250ms ease-out;
  -o-transition: all 250ms ease-out;
}

.hint a:hover {
  color: #FFF;
}

.line-trough {
  text-decoration: line-through;
}

.page .menu__icon {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 10%;
  left: 0;
  width: calc(var(--pixel) * 270);
  height: calc(var(--pixel) * 170);
  margin: auto;
  text-align: center;
  font-size: calc(var(--pixel) * 80);
  line-height: 1.3;
  transform: translateX(360%);
  -webkit-transform: translateX(360%);
  -moz-transform: translateX(360%);
  -o-transform: translateX(360%);
  transition: all 0.5s cubic-bezier(0.25, 1, 0.5, 1.25);
  -webkit-transition: all 0.5s cubic-bezier(0.25, 1, 0.5, 1.25);
  -moz-transition: all 0.5s cubic-bezier(0.25, 1, 0.5, 1.25);
  -o-transition: all 0.5s cubic-bezier(0.25, 1, 0.5, 1.25);
}

.page#p1 .menu__icon {
  height: calc(var(--pixel) * 220);
}

.page#p1 .menu__icon {
  transform: translateX(10%) !important;
}

#t2:target .page#p2 .menu__icon,
#t3:target .page#p3 .menu__icon,
#t4:target .page#p4 .menu__icon,
#t5:target .page#p5 .menu__icon {
  transform: translateX(0) !important;
  -webkit-transform: translateX(0) !important;
  -moz-transform: translateX(0) !important;
  -o-transform: translateX(0) !important;
  transition-delay: 1s;
}

.archive__filder-menu-container-img {
  display: block;
}

.archive__filder-menu-img {
  rotate: 90deg;
  animation: horizontal-shaking 0.35s infinite;
}

.archive__filder-menu-img--rotate {
  padding: calc(var(--pixel) * 10);
  rotate: -90deg;
}

.archive__folder {
  z-index: 99;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: calc(var(--pixel) * 400);
  height: 100%;
  background-color: #FFF;
}

.archive__folder-menu {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  max-width: calc(var(--pixel) * 300);
}

.archive__folder-button {
  width: calc(var(--pixel) * 20);
  height: calc(var(--pixel) * 20);
  margin: calc(var(--pixel) * 10);
}

.archive__file {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.archive__file-menu {
  display: flex;
  justify-content: space-around;
  align-items: center;
  max-width: calc(var(--pixel) * 1000);
  width: calc(var(--pixel) * 1000);
  padding: calc(var(--pixel) * 15) 0;
}

.archive__file-button {
  width: calc(var(--pixel) * 50);
  height: calc(var(--pixel) * 50);
  padding: 0 calc(var(--pixel) * 10);
  cursor: pointer;
}

.scroll-table-body {
  height: calc(var(--pixel) * 700);
  overflow-x: scroll;
  overflow-y: scroll;
  margin-top: 0px;
  margin-bottom: calc(var(--pixel) * 20);
  border-bottom: 1px solid #000000;
}

.scroll-table table {
  width: 90%;
  height: calc(var(--pixel) * 100);
  table-layout: fixed;
  border-collapse: collapse;
  border: none;
}

.scroll-table thead th {
  font-weight: bold;
  text-align: left;
  border: none;
  padding: calc(var(--pixel) * 10) calc(var(--pixel) * 15);
  background: rgba(94, 89, 89, 0.6039215686);
  font-size: calc(var(--pixel) * 14);
  border-left: 1px solid #000000;
}

.scroll-table tbody td {
  text-align: left;
  border-left: 1px solid #000000;
  border-right: 1px solid #000000;
  background: rgba(0, 0, 0, 0.2274509804);
  padding: calc(var(--pixel) * 10) calc(var(--pixel) * 15);
  font-size: calc(var(--pixel) * 14);
  vertical-align: top;
}

.scroll-table tbody tr:nth-child(even) {
  background: #080808;
}

/* Стили для скролла */
::-webkit-scrollbar {
  width: calc(var(--pixel) * 6);
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

::-webkit-scrollbar-thumb {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}